.wrapper {
   font-family: "Roboto", "Helvetica", "Arial", sans-serif;
   line-height: 1.5em;
}

.custom-color {
   color: #555;
}

.sidebar {
   position: fixed;
   top: 0;
   bottom: 0;
   left: 0;
   z-index: 3;
   width: 260px;
   box-shadow: 0 16px 38px -12px rgba(0, 0, 0, 0.56), 0 4px 25px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
   background: linear-gradient(to top, rgba(255, 255, 255, 0.93), rgba(255, 255, 255, 0.93)), url(../img/sidebar-4.jpg);
   height: 100%;
}

.sidebar .logo {
   padding: 15px 8px;
   margin: 0;
   display: block;
   position: relative;
   z-index: 4;
   border-bottom: 1px solid #e3e1e1;
}

.sidebar .logo .simple-text {
   text-transform: uppercase;
   padding: 5px 0px;
   display: inline-block;
   font-size: 18px;
   color: #3C4858;
   white-space: nowrap;
   font-weight: 400;
   line-height: 30px;
   overflow: hidden;
   text-align: center;
   display: block;
}

.caret,
.sidebar a {
   transition: all 150ms ease-in;
}

.sidebar-item li {
   font-weight: 300;
   font-size: 15px;
}

.sidebar-item li.active>a {
   background-color: #f44336;
   box-shadow: 0 4px 20px 0px rgba(0, 0, 0, 0.14), 0 7px 10px -5px rgba(244, 67, 54, 0.4);
   color: #fff !important;
   margin: 0 15px;
   border-radius: 3px;
   font-size: 14.5px;
   padding: 15px;
}

.sidebar-spacing a {
   padding: 0 28px;
}

.main {
   position: relative;
   float: right;
   width: calc(100% - 260px);
   transition: 0.33s, cubic-bezier(0.685, 0.0473, 0.346, 1);
   padding: 10px 15px;
   background-color: #f3f1f1;
   height: 100vh;
}

.heading {
   font-family: "Roboto", "Helvetica", "Arial", sans-serif;
   font-size: 1.5625rem;
   line-height: 1.4em;
   font-weight: 300;
}

.homePage-header {
   font-size: 1rem;
   font-weight: bold;
   background-color: #3f51b5;
   color: #fff;
   padding: 20px;
   margin-bottom: 35px;
   text-align: center;
   margin: 35px 70px;
}

.aboutContact {
   padding: 0.9375rem;
   font-weight: 400;
   font-size: 12.7px;
}

.copyright a {
   color: #9c27b0;
}

.copyright,
.copyright a {
   font-size: 17px;
   font-weight: 300;
}